<template lang="html">
  <div id="orderDesc" class="public">
    <van-nav-bar title="已付订单详情" left-arrow @click-left="onClickLeft"/>
    <van-notice-bar text="商品档期销售未结束的，只要有人继续购买，你即可继续享受共享金额；停止共享时间为销售结束时间为+1天" left-icon="../../static/img/icon-laba.png"/>
    <div class="content-box">
      <van-tabs>
        <van-list v-model="loading" :finished="finished" @load="onLoad">
          <van-tab v-for="index in 3" :title="title[index]">
            <div class="shop-list" v-for="(item,index) in list">
              <div class="box1">
                <span class="fl">{{list[index].title}}</span>
                <span class="fr green" v-if="list[index].static==1">已完成</span>
                <span class="fr red" v-if="list[index].static==0">未完成</span>
              </div>
              <div class="box2">
                <img src="../../static/img/shop-small.png" class="fl img"/>
                <div class="fl content">
                  <p class="desc">{{list[index].desc}}</p>
                  <div class="bottom gray-light font12">
                    <p>口味:<span class="marginL10">{{list[index].guige}}</span></p>
                    <p>可分配剩余时间:<span class="red marginL10">6</span>天<span class="red">11</span>小时<span class="red">13</span>分</p>
                  </div>
                </div>
                <div class="fr alignR gray-light">
                  <p>￥<span class="red">{{list[index].money}}</span></p>
                  <p class="font12 marginT5">x{{list[index].num}}</p>
                </div>
              </div>
              <div class="box3 van-hairline--bottom">
                <span class="fl">预支付金额:<span class="red">{{list[index].pay}}</span></span>
                <span class="marginL10">当前实付:<span class="red">{{list[index].actual}}</span></span>
                <span class="fr">共享金额:<span class="red">{{list[index].share}}</span></span>
              </div>
              <div class="box4 alignC van-hairline--bottom" @click="click($event)" v-if="list[index].distribution==1">共享资金分配详情<i class="icon-arrow-b"></i></div>
              <div class="box4 alignC van-hairline--bottom" v-if="list[index].distribution==0">暂无共享资金分配</div>
              <div class="box5 displayN" v-if="list[index].distribution==1">
                <div class="box5-box">
                  <div class="timeline-item">
                    <span class="fl gray-light">2018-04-11</span>
                    <div class="time"></div>
                    <div class="timeline-icon"></div>
                    <span class="text">当日销量:300</span>
                    <span class="fr red">您的分配:5.00</span>
                  </div>
                  <div class="timeline-item">
                    <span class="fl gray-light">2018-04-11</span>
                    <div class="time"></div>
                    <div class="timeline-icon"></div>
                    <span class="text">当日销量:300</span>
                    <span class="fr red">您的分配:5.00</span>
                  </div>
                  <div class="timeline-item">
                    <span class="fl gray-light">2018-04-11</span>
                    <div class="time"></div>
                    <div class="timeline-icon"></div>
                    <span class="text">当日销量:300</span>
                    <span class="fr red">您的分配:5.00</span>
                  </div>
                  <div class="timeline-item">
                    <span class="fl gray-light">2018-04-11</span>
                    <div class="time"></div>
                    <div class="timeline-icon"></div>
                    <span class="text">当日销量:300</span>
                    <span class="fr red">您的分配:5.00</span>
                  </div>
                  <div class="timeline-item">
                    <span class="fl gray-light">2018-04-11</span>
                    <div class="time"></div>
                    <div class="timeline-icon"></div>
                    <span class="text">当日销量:300</span>
                    <span class="fr red">您的分配:5.00</span>
                  </div>
                  <div class="timeline-item">
                    <span class="fl gray-light">2018-04-11</span>
                    <div class="time"></div>
                    <div class="timeline-icon"></div>
                    <span class="text">当日销量:300</span>
                    <span class="fr red">您的分配:5.00</span>
                  </div>
                  <div class="timeline-item">
                    <span class="fl gray-light">2018-04-11</span>
                    <div class="time"></div>
                    <div class="timeline-icon"></div>
                    <span class="text">当日销量:300</span>
                    <span class="fr red">您的分配:5.00</span>
                  </div>
                </div>
                <div class="box6 gray-light font12">
                  <span>本期总销量:{{list[index].sales}}</span>
                  <span class="text">您的购买位置:230</span>
                </div>
                <div class="box7 bgcW"></div>
              </div>
            </div>
          </van-tab>
        </van-list>
        <div class="gray-light tachBottom">——&nbsp;已经到底儿啦&nbsp;——</div>
      </van-tabs>
    </div>

  </div>
</template>

<script>
import '../assets/css/orderDesc.css'
export default {
  name: 'orderDesc',
  data() {
    return {
        loading: false,
        finished: false,
        title:{
          1:"全部",
          2:"已完成",
          3:"未完成"
        },
        list:[{
          title:'江小白旗舰店',
          img:'../../static/img/shop-small.png',
          static:'1',
          desc:'商品抹茶榴莲起司芒果香蕉牛奶蛋糕1斤点心休闲零食品商品抹茶榴莲起司芒果香蕉牛奶蛋糕1斤点心休闲零食品',
          guige:'芝士味',
          time:'6天11小时13分',
          money:'30.00',
          num:'1',
          sales:'300',
          pay:'29.90',
          actual:28.00,
          share:'11.90',
          distribution:1
        },
        {
          title:'江小白旗舰店',
          img:'../../static/img/shop-small.png',
          static:'0',
          desc:'商品抹茶榴莲起司芒果香蕉牛奶蛋糕1斤点心休闲零食品',
          guige:'芝士味',
          time:'6天11小时13分',
          money:'30.00',
          num:'1',
          sales:'300',
          pay:'29.90',
          actual:29.90,
          share:'0.00',
          distribution:0
        }
      ]
    }
  },
  methods: {
    onLoad() {
      setTimeout(() => {
        console.log(this.list)
        for (var i = 0; i < 2; i++) {
          this.list.push(this.list[i]);
        }
        this.loading = false;

        if (this.list.length >= 10) {
          this.finished = true;
          $(".tachBottom").show();
        }
      }, 1500);
    },
    onClickLeft() {
      window.history.go(-1);
    },
    click(e){
      var el=e.currentTarget;
      $(el).next().slideToggle();
      $(el).find(".icon-arrow-b").toggleClass("flipy");
    }
  },
  mounted(){
    if(this.a=="weixin"){
      $(".van-nav-bar").remove()
      $(".van-notice-bar").css("top","0")
      $(".van-tabs__wrap").addClass("weixin")
      $(".content-box").css("margin-top","40px")
      $(".van-tabs--line").css("padding-top","38px")
    }
  }
}
</script>



