import Vue from 'vue'
import Router from 'vue-router'
import orderDesc from '@/components/orderDesc'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'orderDesc',
    component: orderDesc,
    meta: {
      title: "已付订单详情",
    }
  }]
})


